.registration {
  width: 100%;
  // background-image: url("../images/registration/bg-form-mobil.jpg");
  background-color: $background-program;
  // opacity: 20%;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 50px 0px;

  @media screen and (min-width: 768px) {
    padding-top: 50px;
    padding-bottom: 71px;
  }
  @media screen and (min-width: 1316px) {
    padding-top: 100px;
    padding-bottom: 62px;
  }
  & .container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &-title {
    width: 280px;
    margin-bottom: 30px;
    font-weight: 800;
    font-size: 14px;
    line-height: 1.36;
    text-align: center;
    letter-spacing: 0.03em;
    color: $white-color;

    @media screen and (min-width: 768px) {
      width: 688px;
      font-size: 26px;
      line-height: 1.31;
    }
    @media screen and (min-width: 1316px) {
      margin-bottom: 50px;
      width: 1090px;
      font-size: 46px;
      line-height: 1.3;
    }
  }
  &-text {
    width: 280px;
    font-weight: 600;
    font-size: 12px;
    line-height: 1.25;
    text-align: center;
    letter-spacing: 0.08em;
    margin-bottom: 30px;
    color: $white-color;
    @media screen and (min-width: 768px) {
      width: 452px;
      font-size: 14px;
      line-height: 1.21;
      margin-bottom: 40px;
    }
    @media screen and (min-width: 1316px) {
      width: 1090px;
      font-size: 30px;
      line-height: 1.23;
      margin-bottom: 56px;
    }
  }
  &-span-accent {
    color: $accent-color;
  }
  &-form {
    display: flex;
    flex-direction: column;
  }
  &-form-input {
    width: 280px;
    height: 48px;
    background-color: transparent;
    border: 1px solid $white-color;
    padding-left: 40px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 1, 2;
    letter-spacing: 0.08em;
    color: $white-color;

    &:first-child {
      margin-bottom: 20px;
    }

    @media screen and (min-width: 768px) {
      width: 390px;
      padding-left: 28px;
      font-size: 14px;
      line-height: 1.214;
    }
    @media screen and (min-width: 1316px) {
      width: 478px;
      height: 70px;
      &:first-child {
        margin-bottom: 30px;
      }
    }
  }
  &-button {
    width: 260px;
    height: 48px;
    margin: 0 auto;
    margin-top: 50px;
    padding: 26px;
    background-color: $accent-color;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 1.25;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: space-between;
    letter-spacing: 0.14em;
    text-transform: uppercase;
    color: $white-color;
    border: none;
  }
  @media screen and (min-width: 768px) {
    margin-top: 31px;
  }
  @media screen and (min-width: 1316px) {
    width: 354px;
    height: 70px;
    margin-top: 80px;
    font-size: 14px;
    line-height: 1.214;
  }
}
